import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
// import { Toast } from '@ionic-native/toast';
import { ToasT } from '../../toast-3/toast-3';

//注入服务
import { service } from "./../../service/service";
import { StorageService } from "./../../service/StorageService";

import { EventSolvePage } from "./eventSolve";
import { ChongbaoPage } from "./chongbao";
import { PandectPage } from './pandect';
import { LoginPage } from "./../login/login";

@Component({
	selector: 'page-eventDetails',
	templateUrl: 'eventDetails.html',
	providers: [StorageService, service,ToasT]
})
export class EventDetailsPage {

	user: any;
	eventId: number;
	userId: any;
	token: any;
	type: any;
	any: any;
	url: any;

	result: any;//返回值为2时，不能请求上级协调
	eventList: any[] = [];//所有事件
	workFlow: any;
	beforeimgurl_2: any;
	afterimgurl_2: any;
	img_show = false;
	img_show1 = false;
	show = false;
	nextuserId: any;
	remark: any;

	startTime: any;
	endTime: any;

	constructor(public navCtrl: NavController,
		private StorageService: StorageService,
		private service: service,
		public params: NavParams,
		private toast: ToasT, ) {

		this.user = this.StorageService.read("user");
		this.userId = this.user['userId'];
		this.token = this.user['token'];
		this.eventId = this.params.get('eventId');
		this.type = this.params.get('type');
		this.url = this.service.serviceUrl;
		// this.startTime = this.params.get('startTime');
		// this.endTime = this.params.get('endTime');
	}

	private solve(x: number) {
		this.navCtrl.push(EventSolvePage, {
			eventId: this.eventId,
			solve: x,
		});
	}

	private chongbao(x: number) {
		this.navCtrl.push(ChongbaoPage, {
			eventId: this.eventId,
			solve: x,
		});
	}

	private eventsolve(solve: number) {
		switch (solve) {
			case 61:/* 不受理 */ case 4:/* 合格 */
				var p = "userId=" + this.userId
					+ "&token=" + this.token
					+ "&eventId=" + this.eventId
					+ "&solve=" + solve
				break;

			// case 5:/* 不合格,要退回 */
			// this.nextuserId = this.workFlow[0]['nextUserId'];
			// console.log("this.nextuserId不合格", this.nextuserId)
			// var p = "userId=" + this.userId
			// 	+ "&token=" + this.token
			// 	+ "&eventId=" + this.eventId
			// 	+ "&solve=" + solve
			// 	+ "&nextUserId=" + this.nextuserId
			// break;

			default:
				alert('非已知处理流程，请按照流程操作！');
				break;
		}

		let url = 'http://' + this.service.serviceUrl + '/eventController/solveEventByImg.do';
		this.service.service(url, p).subscribe(
			data => {

				console.log(data);
				if (data['result'] == 1) {
					alert("处理成功");
					this.navCtrl.pop();
				} else if (data['result'] == -1) {
					this.toast.toast2("其他用户登录了您的账号。");
					this.navCtrl.popToRoot();
				} else {
					alert("处理失败");
				}

			},
			err => {
				console.error(err);
				//alert(err);
				alert("处理失败");
			}
		);
	}

	private queryUserdo(t: any) {

		let p = 'userId=' + this.userId
			+ '&token=' + this.token
			+ '&riverId=' + this.eventList[0]['riverId']
			+ '&regionId=' + this.eventList[0]['regionId']
			+ '&organizationId=' + this.user['organizationId']
			+ '&solve=' + t;
		let url = 'http://' + this.service.serviceUrl + '/userController/queryUser.do';
		this.service.service(url, p).subscribe(
			data => {
				if (data['result'] == 0) {
					this.toast.toast2("其他用户登录了您的账号。");
					this.navCtrl.popToRoot();
				}
				else {
					console.log('查询下一级处理人', data);
					if(data['userList'].length!=0){
						this.solve(t);
					}else{
						this.toast.toast2("查无下一级处理人");
					}
					// console.log("this.nextuserList", this.nextuserList);
					// if (data['userList'] != null) {
					// 	this.nextuser = this.nextuserList[0]['realName'];
					// 	this.nextuserId = this.nextuserList[0]['userId'];
					// }
				}
			},
			err => {
				alert('数据异常，请联系运维人员处理！');
				console.error(err);
			}
		)
	}
	ngOnInit() {
		let p = "userId=" + this.userId
			+ "&token=" + this.token
			+ "&eventId=" + this.eventId;
		let url = 'http://' + this.service.serviceUrl + '/eventController/queryWorkflowLog.do';

		this.service.service(url, p).subscribe(
			data => {
				if (data['result'] == -1) {
					alert("该账号已被顶号,请重新登录！");
					this.navCtrl.push(LoginPage);
				}
				else {
					this.eventList = data['eventList'];
					this.workFlow = data['workflowLog'];
					this.remark = this.workFlow[0]['remark'];
					console.log("eventList:", this.eventList);
					console.log("success!");
					console.log("workFlow:", this.workFlow);
				}

			},
			err => {
				console.error(err);
			}
		);

	}


}



