import { Injectable } from '@angular/core';
// import { File } from '@ionic-native/file';
// import { FileTransfer, FileUploadOptions, FileTransferObject } from '@ionic-native/file-transfer';
// import { FileOpener } from '@ionic-native/file-opener';
import { AlertController } from 'ionic-angular';
// import { Toast } from '@ionic-native/toast';
import { Observable } from 'rxjs/Rx';
import { service } from "./../service/service";
// import { AppVersion } from '@ionic-native/app-version';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';

@Injectable()
export class update {
	private serviceUrl: string;
	private imgb: string;
	private url: any;

	// Resolve HTTP using the constructor
	constructor(
		// private file: File,
		// private transfer: FileTransfer,
		// private fileOpener: FileOpener,
		private alertCtrl: AlertController,
		// private toast: Toast,
		private service: service,
		// private appVersion: AppVersion,
	) {
		this.url = this.service.serviceUrl;
	}

	/**
	 * private instance variable to hold base url
	 * @param v1 本机版本
	 * @param v2 服务器版本
	 */
	public update(v1, v2) {
		const apkUrl = 'http://' + this.url + '/android-debug-np.apk';
		this.detectionUpgrade(apkUrl, true, v1, v2); //提示升级
	}

	/**
	 * 提示升级函数
	 * @param apkUrl 更新地址
	 * @param allowChoose 是否强制升级
	 * @param v1 本机版本
	 * @param v2 服务器版本
	 */
	private detectionUpgrade(apkUrl, allowChoose, v1, v2) {
		if (allowChoose) {
			this.alertCtrl.create({
				title: '升级提示',
				subTitle: '发现新版本,版本号:' + v2 + '是否立即升级？',
				buttons: [{
					text: '取消'
				}, {
					text: '确定',
					handler: () => {
						this.downloadApp(apkUrl);
					}
				}]
			}).present();
		} else {
			this.downloadApp(apkUrl);
		}
	}
	private downloadApp(apkUrl) {
		let alert = this.alertCtrl.create({
			title: '下载进度：0%',
			enableBackdropDismiss: false,
			buttons: ['后台下载']
		});
		alert.present();

		// const fileTransfer: FileTransferObject = this.transfer.create();


		//const apk = this.file.externalRootDirectory+this.apk; //apk保存的目录
		// const apk = this.file.externalRootDirectory + 'app.apk'; //apk保存的目录  
		// fileTransfer.download(apkUrl, apk).then(() => {
		// 	this.fileOpener.open(apk, 'application/vnd.android.package-archive').then(() => {
		// 		console.log('File is opened')
		// 	}).catch(e => {
		// 		console.log('Error openening file', e)
		// 	});
		// });
		// fileTransfer.onProgress((event: ProgressEvent) => {
		// 	let num = Math.floor(event.loaded / event.total * 100);
		// 	if (num === 100) {
		// 		alert.dismiss();
		// 	} else {
		// 		let title = document.getElementsByClassName('alert-title')[0];
		// 		title && (title.innerHTML = '下载进度：' + num + '%');
		// 	}
		// });
	}
}


