import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
// import { Toast } from '@ionic-native/toast';
import { ToasT } from '../../toast-3/toast-3';

//注入服务
import { service } from "./../../service/service";
import { StorageService } from "./../../service/StorageService";
//import { NativeStorage } from '@ionic-native/native-storage';

//导入页面
import { EventDetailsPage } from './eventDetails';
import { NewEventPage } from './newEvent';
import { LoginPage } from "./../login/login";



@Component({
	selector: 'page-pandect',
	templateUrl: 'pandect.html',
	providers: [StorageService, service,ToasT]
})
export class PandectPage {

	user: any;
	userId: any;
	token: any;
	reportType: number;
	et: any;
	ee: any;
	ri: any;
	url: any;
	patrolRecordId: any;

	eventList: any[] = [];//所有事件
	eventList1: any[] = [];//待办
	eventList2: any[] = [];//在办
	eventList3: any[] = [];//已办

	pet: string = "WeekTasks";//设置默认选中
	eventType: string = "all";//设置默认选中
	page: any = 1;//用于存储当前页码
	page_daiban: any = 1;//待办页码，默认1
	page_zaiban: any = 1;//在办页码，默认1
	page_yiban: any = 1;//已办页码，默认1
	type: any = 2;//用于存储类型 3 待办 2 在办 1已办

	constructor(public navCtrl: NavController,
		private StorageService: StorageService,
		private service: service,
		public params: NavParams,
		//private nativeStorage: NativeStorage,
		private toast: ToasT) {
		// this.user=this.StorageService.read("user");
		this.url = this.service.serviceUrl;
		this.patrolRecordId = this.params.get('patrolRecordId') == null ? '' : this.params.get('patrolRecordId');
	}

	private eventDetails(x: number, y: number, z: number, a: number, ) {
		this.navCtrl.push(EventDetailsPage, {
			eventId: x,
			type: y,
			startTime: z,
			endTime: a,
		});
	}

	private newEvent() {
		this.navCtrl.push(NewEventPage);
	}
	do(i) {
		this.et = i.eventId;
		this.ee = i.eventType;
		this.ri = i.riverId;
		console.log(22, this.ee);
		console.log(33, this.et);
	}

	ngOnInit() {
		this.change(1, 1);
		this.change(2, 1);
		this.change(3, 1);
	}

	ionViewDidEnter() {

	}

	//用于上拉加载
	doInfinite(infiniteScroll) {
		console.log('加载开始');

		setTimeout(() => {
			if (this.pet == "DailyTasks") {
				this.page_daiban += 1;
				this.type = 3;
				this.change(3, this.page_daiban);
			}
			else if (this.pet == "WeekTasks") {
				this.page_zaiban += 1;
				this.type = 2;
				this.change(2, this.page_zaiban);
			}
			else {
				this.page_yiban += 1;
				this.type = 1;
				this.change(1, this.page_yiban);
			}
			console.log('加载结束');
			infiniteScroll.complete();
		}, 1000);
	}
	//用于执行翻页
	private change(b: any, a: any) { //b 类型 a 页数
		// console.log("类型",b);
		// console.log("页数",a);
		//去掉插件
		// this.nativeStorage.getItem('user').then(
		// 	data => {
		// 		this.user = data['user'];
		// 		this.userId = this.user['userId'];
		// 		let p = "userId=" + this.user['userId']
		// 			+ "&token=" + this.user['token']
		// 			+ "&patrolRecordId=" + this.patrolRecordId
		// 			+ "&type=" + b
		// 			+ "&pageSize=5"
		// 			+ "&pageNo=" + a
		// 			;
		// 		let url = 'http://' + this.service.serviceUrl + '/eventController/queryEvent.do';

		// 		this.service.service(url, p).subscribe(
		// 			data => {
		// 				if (data['result'] == -1) {
		// 					this.toast.showShortBottom("其他用户登录了您的账号。").subscribe();
		// 					this.navCtrl.popToRoot();
		// 				}
		// 				else if (b == 3) {
		// 					for (let i = 0; i < data['eventList'].length; i++) {
		// 						this.eventList1.push(data['eventList'][i]);
		// 					}
		// 					console.log("success!");
		// 					console.log("待办列表:", this.eventList1);
		// 				}
		// 				else if (b == 2) {
		// 					for (let i = 0; i < data['eventList'].length; i++) {
		// 						this.eventList2.push(data['eventList'][i]);
		// 					}
		// 					console.log("success!");
		// 					console.log("在办列表:", this.eventList2);
		// 				}
		// 				else {
		// 					for (let i = 0; i < data['eventList'].length; i++) {
		// 						this.eventList3.push(data['eventList'][i]);
		// 					}
		// 					console.log("success!");
		// 					console.log("已办列表:", this.eventList3);
		// 				}

		// 			},
		// 			err => {
		// 				console.error(err);
		// 			}
		// 		);

		// 	}
		// );
		//自己实现
		let data = this.StorageService.read('user');
		if (data != null) {
			this.user = data['user'];
			this.userId = this.user['userId'];
			let p = "userId=" + this.user['userId']
				+ "&token=" + this.user['token']
				+ "&patrolRecordId=" + this.patrolRecordId
				+ "&type=" + b
				+ "&pageSize=5"
				+ "&pageNo=" + a
				;
			let url = 'http://' + this.service.serviceUrl + '/eventController/queryEvent.do';

			this.service.service(url, p).subscribe(
				data => {
					if (data['result'] == -1) {
						this.toast.toast2("其他用户登录了您的账号。");
						this.navCtrl.popToRoot();
					}
					else if (b == 3) {
						for (let i = 0; i < data['eventList'].length; i++) {
							this.eventList1.push(data['eventList'][i]);
						}
						console.log("success!");
						console.log("待办列表:", this.eventList1);
					}
					else if (b == 2) {
						for (let i = 0; i < data['eventList'].length; i++) {
							this.eventList2.push(data['eventList'][i]);
						}
						console.log("success!");
						console.log("在办列表:", this.eventList2);
					}
					else {
						for (let i = 0; i < data['eventList'].length; i++) {
							this.eventList3.push(data['eventList'][i]);
						}
						console.log("success!");
						console.log("已办列表:", this.eventList3);
					}

				},
				err => {
					console.error(err);
				}
			);
		}
	}
}



